.chat-container {
  display: flex;
  height: calc(100vh - 150px);
  /* width: 100%; */
  padding: 0px;
  border: 1px solid #ccc;
  border-radius: 8px;
  float: left;
  width: calc(100% - 30px);
  margin-left: 15px;
}

.chat-contact-list {
  overflow-y: scroll;
  max-height: 100%;
}

.sidebar {
  width: 200px;
  background-color: #f0f0f0;
  border-radius: 8px 0 0 8px;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
  float: left;
  margin-top: 0;
  margin-bottom: 0;
}

.sidebar li {
  padding: 6px 12px;
  cursor: pointer;
  float: left;
  width: 100%;
  border-bottom: 1px solid lightgray;
}

.sidebar h3 {
  margin-top: 8px;
  margin-bottom: 8px;
  float: left;
  width: 100%;
  text-align: left;
}

.sidebar span {
  float: right;
  font-size: 10px;
  margin-bottom: 5px;
  color: grey;
}

.sidebar li.active {
  background-color: #ddd;
}

.chat-box {
  flex: 1;
  max-width: calc(100% - 200px);
  margin: 10px auto;
}

.agent-selection {
  float: left;
  padding-left: 15px;
  margin-left: 15px;
}

.chat-time {
  float: right;
  width: 100%;
  text-align: right;
  font-size: 10px;
}

@keyframes p-progress-spinner-color {

  100%,
  0% {
    stroke: #d62d20;
  }

  40% {
    stroke: #0057e7;
  }

  66% {
    stroke: #008744;
  }

  80%,
  90% {
    stroke: #ffa700;
  }
}

.p-float-label {
  float: left;
  width: 100%;
}

.chat-history {
  float: left;
  width: 90%;
  overflow-y: auto;
  max-height: 100%;
  margin-left: 5%;
  margin-right: 5%;
}

.message {
  padding: 8px 12px;
  border-radius: 8px;
  max-width: 40%;
}

.sent {
  background-color: #007bff;
  color: white;
  align-self: flex-start;
  float: left;
}

.received {
  background-color: #f0f0f0;
  color: #333;
  float: right;
}

.chat-input {
  margin-top: 20px;
}

.chat-input input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 70%;
  margin-right: 10px;
}

.parent-div {
  float: left;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.chat-input button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.chat-parent {
  width: 94%;
  margin-left: 3%;
  margin-right: 3%;
}









