    height:100vh;
  }

/* Styles for the scrollable table */
.table-scrollable {
    max-height: calc(88vh - 200px); /* Adjust the height as needed */
    overflow-y: auto;
  }
  .card-title{
    margin-bottom: 0px;
  }  
